import React,{useState} from 'react';
import './style.css';

const Todo = () => {

    const [inputData, setInputData] = useState("");
    const [items , setItems] = useState([]);
    
    const addItem = (inputData) =>{

        if(!inputData){
            alert("Please fill the data");
        }else{
            const newItem = {
                id : new Date().getTime().toString(),
                name : inputData
            }
            setItems([...items , newItem]);
            setInputData("");
        }
    }

    const deleteItem = (index) =>{

        const updatedItems = items.filter( (currEle)=>{
            return currEle.id !== index; 
        }); 

        setItems(updatedItems);
    }

    //remove All the items
    const removeAll =()=>{
        setItems([]);
    }

    return (
        <>
            <div className = "main-div">
                
                <div className = "child-div">
                    <figure>
                        <img src = "./images/todo.png" alt = "ToDoLogo" />
                        <figcaption>Add Your List Here ✌</figcaption>
                    </figure>
                    <div className = "addItems">
                        <input 
                            type = "text"
                             placeholder = "✍ Add Item"
                             className = "form-control"
                             value ={inputData}
                             onChange = {(eve) =>setInputData(eve.target.value)}
                        />
                        <i className="fa fa-plus add-btn" onClick = {addItem}></i>
                    </div>

                    {/*show our Items */}
                    <div className = "showItems">
                        {items.map( (currEle) =>{

                            return(
                                <div className = "eachItem" key={currEle.id}>
                                <h3>{currEle.name}</h3>
                                <div className ="todo-btn">
                                    <i className = "far fa-edit add-btn"></i>
                                    <i className ="far fa-trash add-btn"></i>
                                    <i className="far fa-edit" onClick ={ ()=> deleteItem(currEle.id)}></i>
                                </div>
                                </div>
                            );
                        })}                        
                    </div>

                    {/* remove All button */}
                    <div className = "showItems">
                        <button className ="btn effect04" data-sm-link-text = "Remove All"
                        onClick = {removeAll}>
                            <span>CHECK LIST</span>
                        </button>
                    </div>
                </div>
            </div>            
        </>
    )
}

export default Todo
